* {
  box-sizing: border-box;
}

body {
  font-family: system-ui, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Helvetica Neue, Arial, sans-serif;
}

.feedback-statuseffect {
  color: #fff;
  border-radius: 12px;
  margin: .25rem .1rem;
  padding: .3rem .5rem;
  font-size: .9rem;
}

#messages {
  color: #fff;
  background-color: #36365e;
  max-width: 900px;
  margin: .5rem auto;
  padding: 1rem;
}

#messages:before {
  content: "Combat Log";
  text-transform: uppercase;
  color: orange;
  border-bottom: 1px solid orange;
  margin-bottom: .5rem;
  font-size: 1.2em;
  font-weight: 900;
  display: block;
}

#messages strong {
  color: orange;
}

.game-message {
  margin-bottom: .5rem;
}

#errors {
  color: #fff;
  background-color: #8b0000;
  margin: 2rem .5rem .5rem;
  padding: 1rem;
  display: none;
}

#errors.show {
  display: block;
}

#players {
  margin-bottom: 3rem;
  display: flex;
}

#players img {
  width: 40px;
  display: block;
}

#players .avatar-icon {
  width: 100px;
}

.game-players {
  justify-content: center;
  display: flex;
}

#player-indicator {
  flex-direction: column;
  align-items: center;
  font-size: 10rem;
  display: flex;
}

#player-indicator .player-indicator-archer {
  width: 80px;
  margin-top: 40px;
  margin-bottom: 15px;
}

#player-indicator .feedback-damage {
  color: #8b0000;
  margin: 0;
  font-size: 1rem;
  font-weight: 700;
}

#player-indicator .feedback-healing {
  color: #006400;
  margin: 0;
  font-size: 1rem;
  font-weight: 700;
}

.activeplayer {
  outline: 8px solid #ff0;
}

.game-player {
  background-color: #d3d3d3;
  grid-template-columns: 1fr auto;
  grid-template-areas: "player-name avatar-icon"
                       "player-class avatar-icon"
                       "player-health avatar-icon"
                       "player-defense avatar-icon"
                       "player-effects player-effects"
                       "player-buttons player-buttons";
  min-width: 425px;
  margin: .5rem;
  padding: 1rem;
  display: grid;
}

.player-name {
  grid-area: player-name;
  font-size: 2rem;
  font-weight: 700;
}

.player-class {
  grid-area: player-class;
}

.player-health {
  grid-area: player-health;
}

.player-defense {
  grid-area: player-defense;
}

.player-effects {
  grid-area: player-effects;
  gap: .2rem;
  margin: .25rem 0 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.player-effects li span {
  color: #fff;
  background-color: #000000b3;
  border-radius: 10px;
  margin: 0;
  padding: .25rem .5rem;
  font-size: .8em;
}

#players .status-stun, #players .status-burn {
  background-color: #8b0000;
}

#players .status-heal, #players .status-cure {
  background-color: #006400;
}

#players .status-evade, #players .status-riposte {
  background-color: #00008b;
}

.avatar-icon {
  grid-area: avatar-icon;
}

.skill-buttons {
  grid-area: player-buttons;
}

.skill-button-wrapper {
  border: 0;
  flex-direction: row;
  justify-content: center;
  gap: .35rem;
  width: 100%;
  margin: 1rem 0 0;
  padding: 0;
  display: flex;
  position: relative;
}

.player-skill {
  color: #00008b;
  cursor: pointer;
  background-color: #0003;
  border: 0;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-width: 6rem;
  min-height: 4rem;
  margin: 0;
  padding: .5rem;
  display: flex;
}

#players .player-skill:disabled {
  color: gray;
  cursor: default;
}

.player-skill:hover {
  background-color: #0000001a;
}

#players .player-skill:disabled:hover {
  background-color: #0003;
}

.player-effects span, .player-name span, .player-class span, .player-health span, .player-defense span {
  font-weight: 700;
}

.player-skill-tooltip {
  pointer-events: none;
  color: #fff;
  background-color: #000000f2;
  padding: .9rem;
  display: none;
  position: absolute;
  top: 70px;
  left: 0;
  box-shadow: 2px 2px 6px #0009;
}

#players .feedback-skillIcon {
  width: 60px;
}

.player-skill:hover + .player-skill-tooltip {
  display: block;
}

#gameover {
  background-image: url("bg.27048194.png");
  background-repeat: no-repeat;
  background-size: cover;
  border: 0;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: .5rem;
  min-width: 320px;
  min-height: 320px;
  display: flex;
  box-shadow: 3px 3px 10px #0006;
}

#gameover h2 {
  color: #ffffffa6;
  text-shadow: 0 0 10px #000000bf;
  margin: .5rem 0 0;
  font-size: 3.8em;
}

#gameover div {
  color: #00008be6;
  -webkit-text-stroke-width: .5px;
  -webkit-text-stroke-color: #fff;
  text-transform: uppercase;
  margin-bottom: .75rem;
  font-size: 1.7rem;
  font-weight: 900;
}

#gameover::backdrop {
  background-color: #000c;
}

#gameover button {
  color: #0400e0;
  border: 1px solid #000;
  border-radius: 10px;
  width: 100px;
  margin: .3rem 0;
  padding: 5px;
  font-weight: 700;
}

#new-players-form {
  flex-direction: column;
  justify-content: center;
  display: flex;
}

#new-players-wrapper, .start-game-button-wrapper {
  justify-content: center;
  display: flex;
}

#start-game-button {
  padding: 1rem;
  font-size: 1.5em;
}

.new-player-box {
  background-color: #d3d3d3;
  min-width: 250px;
  margin: .5rem;
  padding: 1.5rem;
}

.new-player-box input[type="text"] {
  border: 1px solid #00000080;
  border-radius: 8px;
  padding: .4rem;
}

.new-player-box label:not(.input-box-label) {
  margin-top: 1rem;
  font-weight: 700;
  display: block;
}

.new-player-box h2 {
  margin-top: 0;
}

.input-box-label img {
  border-radius: 50%;
  max-width: 50px;
}

.new-player-class-wrapper {
  align-items: center;
  display: flex;
}

.new-player-class-wrapper label {
  align-items: center;
  gap: .5rem;
  padding: .4rem 1rem .4rem .5rem;
  display: flex;
  position: relative;
  left: -20px;
}

.new-player-class-wrapper input {
  visibility: hidden;
}

.new-player-class-wrapper input:checked ~ label {
  background-color: #00000040;
  border-radius: 15px;
}

.fighterclass-warrior {
  background-color: #d99898;
}

.fighterclass-mage {
  background-color: #add8e6;
}

.fighterclass-rogue {
  background-color: #90ee90;
}

.fighterclass-mage.activeplayer {
  outline: 8px solid #00f;
}

.fighterclass-rogue.activeplayer {
  outline: 8px solid green;
}

.fighterclass-warrior.activeplayer {
  outline: 8px solid red;
}

.fighterclass-warrior .player-skill {
  color: #8b0000;
}

.fighterclass-mage .player-skill {
  color: #00008b;
}

.fighterclass-rogue .player-skill {
  color: #006400;
}

